class ValidForm {
    constructor() {
        this.validUserFormReg = {

            //用户名1-10个字符,汉字字母数字_组合
            nickName: {
                reg: /^[\u4e00-\u9fa5\w]{1,10}$/,
                msg: "用户名格式不正确",
            },

            //手机号
            phone: {
                reg: /^1[3-9]\d{9}$/,
                msg: "手机格式不正确"
            },
            //密码，首字符为字母，其他字符为数字字母下划线组合，6-16个字符
            password: {
                reg: /^[a-zA-Z]\w{5,15}$/,
                msg: "密码格式不正确"
            },
            // 旧密码
            oldPassword: {
                reg: /^[a-zA-Z]\w{5,15}$/,
                msg: '旧密码格式不正确且以字母开头'
            },

            //邮箱
            email: {
                reg: /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/,
                msg: '邮箱格式不正确'
            },

            //6位数字验证码
            code: {
                reg: /^\d{6}$/,
                msg: '验证格式正确'
            }
        }
    }

    //验证表单
    validUserForm(o, self) {
        for (let key in o) {
            if (!this.validUserFormReg[key].reg.test(o[key])) {
                self.$toast(this.validUserFormReg[key].msg);
                return false;
            }
        }
        //表单验证通过
        return true;
    }
}
export default new ValidForm();