<template>
  <div class="secure">
    <van-nav-bar title="安全中心" left-text="返回" left-arrow @click-left="back" />
    <div class="listBox">
      <van-cell-group>
        <van-cell title="修改密码" is-link @click="openPopup" />
        <van-cell title="注销账号" is-link @click=" destroyAccount" />
      </van-cell-group>
    </div>
    <div class="logout">
      <van-button :round="true" :block="true" type="danger" @click="logout">退出登录</van-button>
    </div>
    <van-popup v-model="isOpen" position="bottom">
      <div class="v-pop">
        <van-form @submit="commit">
          <van-field v-model="passwords.oldPassword" label="旧密码" placeholder="旧密码" />
          <van-field v-model="passwords.password" type="text" label="新密码" placeholder="新密码" />
          <div class="btnBox">
            <van-button round block type="info" native-type="submit">提交</van-button>
          </div>
        </van-form>
      </div>
    </van-popup>
  </div>
</template>
<script>
import validForm from "../assets/js/ValidForm";
export default {
  data() {
    return {
      isOpen: false,
      passwords: {
        oldPassword: "",
        password: ""
      }
    };
  },
  methods: {
    //返回
    back() {
      this.$router.go(-1);
    },
    //显示修改密码框
    openPopup() {
      this.isOpen = true;
    },
    //提交修改密码
    commit() {
      if (!validForm.validUserForm(this.passwords)) {
        return;
      }
      //获取token
      let tokenString = localStorage.getItem("__TK");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });
      this.axios({
        method: "POST",
        url: "/updatePassword",
        data: {
          appkey: this.appkey,
          tokenString,
          //新的密码
          password: this.passwords.password,
          //旧密码
          oldPassword: this.passwords.oldPassword
        }
      })
        .then(result => {
          //关闭加载提示
          this.$toast.clear();
          //关闭修改密码弹出框
          this.isOpen = false;
          if (result.data.code == "E001") {
            //清除token
            localStorage.removeItem("__TK");
            this.$router.push({ name: "Login" });
          } else {
            this.$toast(result.data.msg);
          }
        })
        .catch(err => {
          //关闭加载提示
          this.$toast.clear();
        });
    },
    //退出登录
    logout() {
      localStorage.removeItem("__TK");
      this.$router.push({ name: "Login" });
    },
    //注销
    destroyAccount() {
      this.$dialog
        .confirm({
          title: "注销账号",
          message: "一旦注销，数据无法恢复"
        })
        .then(() => {
          // on confirm
          //发起注销请求
          //获取token
          let tokenString = localStorage.getItem("__TK");
          this.$toast.loading({
            message: "加载中...",
            forbidClick: true,
            //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
            duration: 0
          });
          this.axios({
            method: "POST",
            url: "/destroyAccount",
            data: {
              appkey: this.appkey,
              tokenString
            }
          })
            .then(result => {
              //关闭加载提示
              this.$toast.clear();
              if (result.data.code == "G001") {
                //清除token
                localStorage.removeItem("__TK");
                this.$router.push({ name: "Login" });
              } else {
                this.$toast(result.data.msg);
              }
            })
            .catch(err => {
              //关闭加载提示
              this.$toast.clear();
            });
        })
        .catch(err => {});
    }
  }
};
</script>
<style lang="less">
.secure {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background-color: #fff;
  .listBox {
    margin-top: 10px;
  }
  .logout {
    margin-top: 60px;
    padding: 0 20px;
  }
  .v-pop {
    padding: 20px;
  }
  .btnBox {
    margin-top: 20px;
  }
  /deep/ .van-dialog__confirm,
  .van-dialog__confirm:active,
  .van-button--default {
    color: brown;
  }
  /deep/ .van-dialog {
    .van-dialog__confirm,
    .van-dialog__confirm:active,
    .van-button--default {
      color: brown;
    }
  }
  /deep/ .van-button--info {
    color: #fff;
    background-image: linear-gradient(rgb(215, 180, 148), brown);
    border: none;
  }
  /deep/ .van-cell-group {
    padding: 10px;
  }
  .van-cell {
    margin-bottom: 10px;
    border: 1px solid rgb(215, 180, 148);
    border-radius: 10px;
  }
}
</style>